<template>
	<view class="">
		<uni-rate value="">sdjns</uni-rate>
		<view >
			{{msg}}
		</view>
		<button type="default" @click="clickMe">clickME</button>
		<view v-for="(item,index) in arr" :key="index">
			name:{{item.name}} age:{{item.age}}
		</view>
		<button type="default" @click="startPull"></button>
		<view >
			active issue:{{station}}
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
			    arr: [
			      { name: '刘能', age: 29 },
			      { name: '赵四', age: 39 },
			      { name: '宋小宝', age: 49 },
			      { name: '小沈阳', age: 59 }
			    ],
				msg:"hello OA",
				station:"none"
				}
		},
		methods:{
			clickMe(){
				this.msg='oHHH OA';
			},
			startPull(){
				uni.startPullDownRefresh();
			}
		},
			
		onPullDownRefresh(){
			this.arr=[];
			setTimeout(()=>{
				this.arr=[
			      { name: '刘', age: 29 },
			      { name: '赵', age: 39 },
			      { name: '宋小', age: 49 },
			      { name: '小沈', age: 59 }
			    ];
				uni.stopPullDownRefresh();
			},1000)
			this.station="onPullDownRefresh done"
		}
	}
</script>

